import React from 'react';

import BannerComponent from '../_reusable/banner/BannerComponent';

function HelpComponent() {
  return (
    <div>
      <BannerComponent
        componentName="Help"
        isAddButtonHidden
        isSearchHidden
      />
      <hr style={{ background: '#c8c8c8' }} />
      Contact
      {' '}
      <a href="mailto:support@virtualai.io" style={{ color: '#0d6efd' }}>
        support@virtualai.io
      </a>
      {' '}
      if you need any assistance.
    </div>
  );
}

export default HelpComponent;
